<script>
  // Firebase
  import { firestore } from '../firebase'
  export let status
  export let id
  export let type
  const collectionTypes = {
    light: {
      collectionName: 'lights',
    },
    plug: {
      collectionName: 'plugs',
    },
    door: {
      collectionName: 'doors',
    },
  }
  function toggleStatus() {
    status = !status
    firestore
      .collection('devices')
      .doc(collectionTypes[type].collectionName)
      .update({
        [id]: status,
      })
  }
</script>

<style>button {
  -webkit-transition: all 120ms cubic-bezier(0.5, 0.82, 0.165, 1);
  transition: all 120ms cubic-bezier(0.5, 0.82, 0.165, 1);
  background-color: #2d3748;
  margin: 0.5rem;
  width: 2rem;
  height: 2rem;
  border-radius: 0.25rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

button:hover {
  background-color: #4a5568;
}

.active {
  background: #fff;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<button on:click={toggleStatus} class={status ? 'active' : ''} />
