<script>
  import { onMount } from 'svelte'
  import { saveMatrix } from '../helpers/firestore'
  $: character = []
  const matrixSize = 8

  const generateCharacter = () => {
    let newChar = []
    for (let x = 0; x < Math.pow(matrixSize, 2); x++) {
      newChar.push(Math.random() > 0.5 ? 1 : 0)
    }
    character = newChar
  }

  onMount(() => {
    generateCharacter()
  })
</script>

